import {
  Component, OnInit, Input, ViewChild, ElementRef, Output,
  EventEmitter, OnChanges, SimpleChanges, ViewChildren
} from '@angular/core';
import { ElementPropertyConfig, PropertyChangeObject } from '../../entity/property-entity';
import { PropertyItemListComponent } from '../property-item-list/property-item-list.component';

@Component({
  selector: 'app-property-panel',
  templateUrl: './property-panel.component.html',
  styleUrls: ['./property-panel.component.css']
})
export class PropertyPanelComponent implements OnInit, OnChanges {
  @Input() width: number;
  @Input() height: number;

  /**  */
  @Input() isPersitOpenState = false;

  /** isPersitOpenState=true */
  @Input() isShowPanel = false;

  /** isPersitOpenState=true */
  @Output() closePropertyPanel = new EventEmitter<any>();

  /**  */
  @Input() propertyConfig: ElementPropertyConfig[];

  /**   */
  @Input() propertyData = {};

  /**  */
  @Output() propertyChanged = new EventEmitter<any>();

  /**    */
  @Output() submitModal = new EventEmitter<any>();

  /**  */
  @Input() showCloseBtn = true;


  @ViewChild('east') private eastDiv: ElementRef;
  @ViewChildren(PropertyItemListComponent) itemLists: Array<PropertyItemListComponent>;

  /**  */
  isOpen = true;

  /**  */
  categoryTabs = [];

  /**  */
  selectedTab: any;

  constructor() { }

  ngOnInit() {

    if (this.width) {
      this.eastDiv.nativeElement.style.width = this.width + 'px';
    } else {
      this.eastDiv.nativeElement.style.width = 'inherit';
    }
    if (this.height) {
      this.eastDiv.nativeElement.style.height = this.height + 'px';
    } else {
      this.eastDiv.nativeElement.style.height = '100%';
    }


    if (this.isPersitOpenState) {
      this.isOpen = this.isShowPanel;
    }

    this.checkShowTabCategory();
  }

  /**
   *  isPersitOpenState=true
   */
  ngOnChanges(simpleChanges: SimpleChanges) {
    if (simpleChanges.isShowPanel && this.isPersitOpenState) {
      this.isOpen = this.isShowPanel;
    }
    this.checkShowTabCategory();
  }
  refreshPanel() {
    this.checkShowTabCategory();
    this.itemLists.forEach(item => item.refresh());
  }
  changeStatus(item: any) {
    if (this.propertyConfig.length < 2) {
      return;
    }
    if (!item.status || item.status === 'open') {
      item.status = 'closed';
    } else {
      item.status = 'open';
    }
  }

  /**
   *  
   */
  collapse() {
    // isPersitOpenState=true,
    if (!this.isPersitOpenState) {
      this.isOpen = false;
    }
    this.closePropertyPanel.emit();
  }

  /**
   * 
   */
  _valueChanged(changeObject: PropertyChangeObject) {
    this.propertyChanged.emit(changeObject);
  }
  _submitModal($event: PropertyChangeObject) {
    this.submitModal.emit($event);
  }

  /**
   * 
   */
  private checkShowTabCategory() {
    this.categoryTabs = [];
    if (!this.propertyConfig || this.propertyConfig.length === 0) {
      this.categoryTabs = [{
        tabId: 'default',
        tabName: '',
        categoryList: []
      }];
      this.selectedTab = null;
      return;
    }
    this.propertyConfig.forEach(config => {
      if (config.tabId) {
        const propTab = this.categoryTabs.find(t => t.tabId === config.tabId);
        if (!propTab) {
          this.categoryTabs.push({
            tabId: config.tabId,
            tabName: config.tabName,
            categoryList: [config],
            hide: config.hide || config.properties.length === 0
          });
        } else {

          propTab.categoryList.push(config);
          if (propTab.hide) {
            propTab.hide = config.hide || config.properties.length === 0;
          }
        }
      } else {
        const defaultTab = this.categoryTabs.find(t => t.tabId === 'default');
        if (!defaultTab) {
          this.categoryTabs.push({
            tabId: 'default',
            tabName: '',
            categoryList: [config]
          });
        } else {
          defaultTab.categoryList.push(config);
        }

      }
    });

    this.selectedTab = this.categoryTabs[0];


  }
}
