import { Type } from '@angular/core';

/**
 * 
 */
export enum PropertyType {
    string = 'string',
    boolean = 'boolean',
    number = 'number',
    select = 'select',
    boolOrExp = 'boolOrExp',
    multiSelect = 'multiSelect',
    date = 'date',
    datetime = 'datetime',
    modal = 'modal',
    cascade = 'cascade'
}


/**
 * 
 */
export class ElementPropertyConfig {
    /**
     * ID
     */
    categoryId: string;

    /**
     * 
     */
    categoryName: string;

    /**
     * false
     */
    hide?= false;

    /**
     * 
     */
    hideTitle?= false;

    /**
     * 
     */
    properties: PropertyEntity[];

    /**
     * false
     */
    enableCascade?= false;

    /**
     * 
     */
    propertyData?: any;

    /**
     * ID
     */
    parentPropertyID?: string;

    /**
     * 
     */
    setPropertyRelates?: (changeObject, propertyData, parameters?) => void;

    /**
     * ID
     */
    tabId?: string;

    /**
     * 
     */
    tabName?: string;
}



/**
 * 
 */
export class PropertyEntity {
    /**
     * ID
     */
    propertyID: string;

    /**
     * 
     */
    propertyName: string;

    /**
     * 
     */
    propertyType: PropertyType | any;

    /**
     * 
     */
    description?: string;

    /**
     * 
     */
    defaultValue?: any;

    /**
     * false
     */
    readonly?= false;

    /**
     * true
     */
    visible?= true;

    /**
     * 
     */
    min?: any;

    /**
     * 
     */
    max?: any;

    /**
     * 
     */
    decimals?: number;

    /**
     * 
     */
    isBigNumber?= false;

    /**
     * 
     */
    refreshPanelAfterChanged?= false;

    /**
     * 
     */
    iterator?: KeyMap[];

    /**
     * string()/array()
     */
    multiSelectDataType?= 'string';

    /**
     * 
     */
    notAllowedChars?: any[];

    /**
     * 
     */
    cascadeConfig?: PropertyEntity[];

    /**
     * 
     */
    isExpand?= false;

    /**
     * 
     */
    hideCascadeTitle?= false;

    /**
     * 
     */
    cascadeConverter?: TypeConverter;

    /**
     * 
     */
    editor?: Type<any>;

    /**
     * 
     */
    editorParams?: any;

    /**
     *  
     */
    converter?: TypeConverter;

    /**
     * 
     */
    showClearButton?= false;

    /**
     * 
     */
    afterClickClearButton?(value: any): void;

    /**
     * result=trueresult=falsemessage
     */
    beforeOpenModal?(): BeforeOpenModalResult {
        return new BeforeOpenModalResult();
    }

    [propName: string]: any;
}

/**
 * 
 */
export interface TypeConverter {
    convertTo(data: any, params?: any): string; // 
}

export interface KeyMap {
    key: any;
    value: any;
}

/**
 * result=trueresult=falsemessage
 */
export class BeforeOpenModalResult {
    result: boolean;
    message?: string;
}

/**
 * 
 */
export class PropertyChangeObject {
    /**
     * ID
     */
    propertyID: string;

    /**
     * 
     */
    propertyValue: any;

    /**
     *  ID
     */
    categoryId: string;

    /**
     * .
     */
    propertyPath: string;

    /**
     * ID
     */
    parentPropertyID: string;

}
