    getValueByObj(field: string, data: any) {
        if (!data) {
            return '';
        }
        let resultVal = '';
        if (field.indexOf('.') === -1) {
            resultVal = data[field];
        } else {
            resultVal = field.split('.').reduce((obj, key) => {
                if (obj) {
                    return obj[key];
                } else {
                    return null;
                }
            }, data);
        }
        return resultVal;
    }

